














































export default {
	data() {
		return {
			user: '',
			password: '',
			isShowOauth: false,
			showProvider: {
				weixin: false,
				qq: false,
				sinaweibo: false,
				xiaomi: false
			},
			isWechatMiniProgram: false,
			recommendWechatLogin: false
		}
	},
	onShow() {},
	onLoad() {
		//APP显示第三方登录




		this.isShowOauth = true;
		this.isWechatMiniProgram = true
		this.recommendWechatLogin = true
		this.wechatLogin()

		this.getProvider();
	},
	methods: {
		onGetUserInfo(e) {
			console.log(e)
		},
		oauthLogin(provider) {
			let that = this
			uni.showLoading();
			//第三方登录
			uni.login({
				provider: provider,
				success: (loginRes) => {
					console.log("success: " + JSON.stringify(loginRes));
					let code = loginRes.code
					//案例直接获取用户信息，一般不是在APP端直接获取用户信息，比如微信，获取一个code，传递给后端，后端再去请求微信服务器获取用户信息
					uni.getUserInfo({
						provider: provider,
						success: (infoRes) => {
							console.log('用户信息：' + JSON.stringify(infoRes.userInfo));
							let userInfo = infoRes.userInfo
							userInfo.code = code
							that.$pin.request('POST', '/sign-in/', userInfo,
							successData=>{
								
							},
							failData => {
								
							})
						}
					});
				},
				fail: (e) => {
					console.log("fail: " + JSON.stringify(e));
				}
			});
		},
		getProvider() {
			//获取第三方登录服务
			uni.getProvider({
				service: 'oauth',
				success: (res) => {
					let len = res.provider.length;
					for (let i = 0; i < len; i++) {
						//有服务才显示按钮图标
						this.showProvider[res.provider[i]] = true;
					}
					if (res.provider.length == 0) {
						this.isShowOauth = false;
					}
				}
			});
		},
		toPage(page) {
			uni.hideKeyboard()
			uni.navigateTo({
				url: page
			});
		},
		loginSuccess() {
			uni.showToast({
				title: '登录成功'
			})
			let pages = getCurrentPages()
			console.log(pages)
			if(pages.length > 1) {
				uni.navigateBack(-1)
			} else {
				uni.switchTab({
					url: "/pages/tab-bar/home"
				})
			}
		},
		loginFailed() {
			
		},
		wechatLogin() {
			console.log("LOGINING WECHAT UNI")
			
			this.$pin.loginFromWechat(this.loginSuccess, this.loginFailed)
			
			console.log("LOGINING WECHAT")
		},
		doLogin() {
			uni.hideKeyboard();
			//验证手机号码
			if (!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.user)) && !(
					/^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/.test(this.user))) {
				uni.showToast({
					title: '请填写正确手机号或邮箱地址',
					icon: "none"
				});
				return false;
			}
			uni.showLoading({
				title: '登录中...'
			})
			let that = this
			this.$pin.request('POST', '/sign-in/default', {
					user: this.user,
					password: this.password
				},
				successData => {
					uni.hideLoading()
					console.log(successData)
					if (successData.code == that.$pin.code.invalidCredential) {
						// 登录失败
						uni.showToast({
							title: '登录凭据错误，请重试。',
							icon: 'none'
						})
					} else if (successData.code == that.$pin.code.success) {
						uni.setStorageSync('pin-token', successData.data)
						that.loginSuccess()
					}
				},
				failData => {
					uni.hideLoading()
				}
			)
		}
	}
}
